.wrapper {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  min-width: 320px;
  max-width: 1024px;
  height: 100%;
  z-index: 999;
  margin: auto;
  background-color: #FFF;
}
.wrapper_contain {
  height: 100%;
  display: flex;
  flex-direction: column;
}
.wrapper_contain_map {
  flex: 1;
}
.wrapper_contain_info {
  padding: 10px 15px;
  position: relative;
}
.wrapper_contain_info::before,
.wrapper_contain_info::after {
  content: '';
  position: absolute;
  display: block;
  z-index: 1;
  width: 100%;
  border-top: 1px solid #DDD;
  left: 0;
  right: 0;
}
.wrapper_contain_info::before {
  top: 0;
}
.wrapper_contain_info::after {
  display: none;
  bottom: 0;
}
@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 2dppx) {
  .wrapper_contain_info::before,
  .wrapper_contain_info::after {
    transform: scaleY(0.5);
  }
}
@media (-webkit-min-device-pixel-ratio: 2) and (-webkit-min-device-pixel-ratio: 3), (min-resolution: 2dppx) and (min-resolution: 3dppx) {
  .wrapper_contain_info::before,
  .wrapper_contain_info::after {
    transform: scaleY(0.33);
  }
}
.wrapper_contain_info_title {
  display: flex;
}
@media screen and (max-width: 321px) {
  .wrapper_contain_info_title {
    /*小*/
    font-size: 16px;
  }
}
@media screen and (min-width: 321px) and (max-width: 400px) {
  .wrapper_contain_info_title {
    /*中*/
    font-size: 17px;
  }
}
@media screen and (min-width: 400px) {
  .wrapper_contain_info_title {
    /*大*/
    font-size: 19px;
  }
}
.wrapper_contain_info_title_pos {
  flex: 1;
}
.wrapper_contain_info_title_pos_tip {
  margin-left: 6px;
  color: #FF0000;
}
@media screen and (max-width: 321px) {
  .wrapper_contain_info_title_pos_tip {
    /*小*/
    font-size: 12px;
  }
}
@media screen and (min-width: 321px) and (max-width: 400px) {
  .wrapper_contain_info_title_pos_tip {
    /*中*/
    font-size: 13px;
  }
}
@media screen and (min-width: 400px) {
  .wrapper_contain_info_title_pos_tip {
    /*大*/
    font-size: 15px;
  }
}
.wrapper_contain_info_address {
  color: #AEAEAE;
}
@media screen and (max-width: 321px) {
  .wrapper_contain_info_address {
    /*小*/
    font-size: 12px;
  }
}
@media screen and (min-width: 321px) and (max-width: 400px) {
  .wrapper_contain_info_address {
    /*中*/
    font-size: 13px;
  }
}
@media screen and (min-width: 400px) {
  .wrapper_contain_info_address {
    /*大*/
    font-size: 15px;
  }
}
.wrapper_contain_info_detail {
  display: flex;
}
.wrapper_contain_info_detail_des {
  flex: 1;
  color: #AEAEAE;
}
@media screen and (max-width: 321px) {
  .wrapper_contain_info_detail_des {
    /*小*/
    font-size: 12px;
  }
}
@media screen and (min-width: 321px) and (max-width: 400px) {
  .wrapper_contain_info_detail_des {
    /*中*/
    font-size: 13px;
  }
}
@media screen and (min-width: 400px) {
  .wrapper_contain_info_detail_des {
    /*大*/
    font-size: 15px;
  }
}
.wrapper_contain_info_detail_go {
  align-self: flex-end;
}
